/* ? ======= hero start ======= */
.hero {
   width: 100%;
   height: 800px;
   background-image: url("../img/hero-bg.webp");
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   padding-top: 208px;
   position: relative;

   &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: var(--color-black);
      opacity: 0.6;
      z-index: -1px;
   }

   &__content {
      color: var(--color-white);
      position: relative;
      z-index: 1;

      display: flex;
      justify-content: space-between;
      align-items: flex-start;
   }

   &__content-left,
   &__content-right {
      width: 50%;
   }

   &__title {
      margin: 0;
      font-style: normal;
      font-weight: 700;
      font-size: clamp(2.25rem, 1.893rem + 1.79vw, 3.5rem);
      letter-spacing: 0.16em;
   }

   &__subtitle {
      margin-top: 30px;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      letter-spacing: 0.1em;
   }

   &__list {
      margin-left: 50px;
   }

   &__item {
      font-style: normal;
      font-weight: 400;
      font-size: clamp(1rem, 0.857rem + 0.71vw, 1.5rem);
      line-height: 150%;
      letter-spacing: 0.1em;

      &:not(:last-child) {
         margin-bottom: clamp(0.063rem, -0.188rem + 1.25vw, 0.938rem);
      }
   }

   &__btn {
      position: absolute;
      z-index: 4;
      margin-top: 148px;
      left: 50%;
      transform: translateX(-50%);
      transition: 0.5s;

      &:hover {
         background-color: var(--color-lochinvar);
         transition: 0.5s;
      }
   }
}

/* ? ======= hero media start ======= */

@media screen and (max-width: 1220px) {
   .hero {
      &__list {
         margin: 0;
      }
   }
}

@media screen and (max-width: 870px) {
   .hero {
      padding-top: 100px;

      &__subtitle {
         max-width: 400px;
      }

      &__list {
         list-style: none;
         padding: 0;
      }

      &__content {
         flex-direction: column;
         align-items: center;
         gap: 50px;
      }

      &__content-left,
      &__content-right {
         width: 100%;
      }

      &__btn {
         margin-top: 100px;
      }
   }
}
