/* ? ======= header start ======= */
.header {
   width: 100%;
   height: 130px;
   position: absolute;
   z-index: 1;
   left: 0;
   top: 0;

   &__flex {
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;

      position: relative;
   }

   &__logo {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;

      img {
         margin-bottom: 5px;
      }
   }

   &__logo-text {
      width: 160px;
      margin-left: 13px;

      font-family: var(--font-family);
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      color: var(--color-white);
   }

   &__menu {
      height: 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 50px;
   }

   &__item {
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 20px;
      position: relative;
      color: var(--color-white);
      transition: 0.5s;

      &:before {
         position: absolute;
         content: "";
         width: 0%;
         height: 2px;
         bottom: -5px;
         left: 0;
         transition: 0.5s;
      }

      &:hover:before {
         transition: 0.5s;
         width: 100%;
         background-color: var(--color-keppel);
      }

      &:hover {
         color: var(--color-keppel);
         transition: 0.5s;
      }

   }

   &__burger {
      display: none;
   }

   &__phone {
      height: 40px;
      font-family: "Ubuntu";
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 20px;
      color: var(--color-white);
      transition: 0.5s;

      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 10px;

      &:hover {
         color: var(--color-keppel);
         transition: 0.5s;
      }

      &:hover use {
         fill: var(--color-keppel);
         transition: 0.5s;
      }
   }

   &__phone-svg {
      width: 32px;
      height: 32px;
      margin-right: 10px;

      use {
         fill: var(--color-white);
         transition: 0.5s;
      }
   }
}

/* ? ======= header start js-classes======= */

.js-burger--active > .header__burger-line {
   position: absolute;
   background-color: transparent;
   transition: 0.5s;
}

.js-burger--active > .header__burger-line::after {
   transform: rotate(45deg);
   transition: 0.5s;
}

.js-burger--active > .header__burger-line::before {
   transform: rotate(-45deg);
   transition: 0.5s;
}

.header__menu.js-menu--active {
   background-color: var(--color-black);
   left: 50%;
   transform: translate(-50%, 0);
   transition: 1s;
   width: 100%;
   height: 100vh;
   position: absolute;
   z-index: 4;
}

.header__menu.js-menu--active > .header__item {
   margin-top: 0px;
   margin-bottom: 0px;
   font-size: 26px;
}

/* ? ======= header start media ======= */
@media screen and (max-width: 1125px) {
   .header {
      height: auto;
      padding-top: 20px;
      padding-bottom: 20px;
      &__menu {
         flex-direction: column;
         justify-content: flex-start;
         padding-top: 30px;
         position: absolute;
         left: -150%;
         min-width: 320px;
         top: 60px;
         transition: 1s;
      }

      &__burger {
         width: 30px;
         height: 20px;

         display: flex;
         flex-direction: column;
         justify-content: center;

         position: relative;
         bottom: 5px;
      }

      &__burger-line {
         width: 100%;
         height: 2px;
         background-color: var(--color-white);
         transition: 0.5s;

         &:before,
         &:after {
            position: absolute;
            content: "";
            left: 0;
            width: 100%;
            height: 2px;
            background-color: var(--color-white);
         }

         &:after {
            top: 0;
            transition: 0.5s;
         }

         &:before {
            bottom: 0;
            transition: 0.5s;
         }
      }

      &__phone {
         margin-bottom: 5px;
      }
   }
}

@media screen and (max-width: 550px) {
   .header {
      &__phone > span {
         display: none;
      }
   }
}

@media screen and (max-width: 375px) {
   .header {
      &__logo-text {
         display: none;
      }
   }
}
